body{
  margin: 0;
  padding: 0;

  .mainData{
    display: flex;
    flex-direction: column;
    align-items: center;

    .sendCSV{
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-top: 25px;
      margin-bottom: 25px;
      border: 2px dashed gray;
      border-radius: 10px;

      input{
        padding: 10px;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
        cursor: pointer;
        outline: none;

        &:hover{
          background-color: rgba(3, 89, 132, 0.1);
          border-radius: 10px;
        }
      }

      button{
        border: none;
        margin: 8px;
        cursor: pointer;
        background-color: #fff;
        font-size: 15px;
        outline: none;

        &:hover{
          margin: 0;
          background-color: rgba(3, 89, 132, 0.1);
          border-radius: 10px;
          padding: 12px 14px;
        }
      }
    }

    .downloadCSV{
      margin-bottom: 25px;

      button{
        height: 45px;
        width: 150px;
        border: 2px solid gray;
        border-radius: 10px;
        background-color: white;
        cursor: pointer;
        outline: none;

        &:hover{
          background-color: rgba(255, 153, 0, 0.1);
          border-radius: 10px;
        }
      }
    }
  }

  .getJSON{

    button{
      margin-bottom: 10px;
      height: 45px;
      width: 150px;
      border: 2px solid gray;
      border-radius: 10px;
      background-color: white;
      cursor: pointer;
      outline: none;
      &:hover{
        background-color: rgba(20, 247, 65, 0.1);
        border-radius: 10px;
      }
    }

    div{
      display: flex;
      flex-direction: column;
      align-items: center;

      table{
        width: 400px;
        border: 1px solid grey;
        border-collapse: collapse;
        text-align: center;
        td{
          padding: 5px;
        }
        th, td {border: 1px solid grey;}
      }
    }
  }
}